* {
    box-sizing: border-box;
}
.App {
    width:800px;
    max-width: 100%;
    margin:10px auto;
}

.App input[type="text"] {
    font-size: 1.5em;
    padding: 7px;
    border: 1px solid rgba(0,0,0,0.2);
    margin:0px 10px 0px 0px;
    min-width: 200px;
    max-width: 100%;
    width:400px;
    height: 45px;
    border-radius: 3px;
}

.App button {
    font-size: 1.5em;
    text-align: center;
    background-color: gainsboro;
    border:none;
    cursor: pointer;
    border-radius: 5px;
    height: 45px;
    padding: 0px 25px;
    transition: all 0.2s ease-in-out;
}

.App button:hover {
    background-color: lawngreen;
}

.App button.disabled,
.App button.disabled:hover {
    cursor:not-allowed;
    background-color: lightslategray;
}

.App button.connected {
    background-color:red;
    color: white;
}

.App button.connected:hover {
    background-color: lightcoral;
}

.App input.disabled {
    cursor:not-allowed;
    background-color: lightslategray;
}

.App span.disconnected {
    color:red
}

.App span.connected {
    color:green;
}