* {
    box-sizing: border-box;
}

.CLIView {
    display: block;
    width: 800px;
    height: 600px;
    max-width: 100%;
    background-color: black;
    border:1px solid rgba(255,255,255,0.2);
    margin:10px 0px;
    color:green;
    padding:10px 10px 100px 10px;
    overflow-y: scroll;
    font-size: 1em;
}

.CLIView input[type="text"] {
    padding:0px;
    margin:0px;
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    display:block;
    font-size: 1em;
    height: auto;
}

.output-line {
    margin:5px 0px;
    color:green;
}

.output-line.input {
    color:white;
}